import { Component, OnInit } from '@angular/core';
import { Ad } from '@core/models';
import { DestroyService } from '@shared/services/destroy.service';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { Observable, of } from 'rxjs';
import { FavoritesFacade } from './favorites.facade';

@Component({
    selector: 'findqo-favorites.page',
    templateUrl: './favorites.page.html',
    styleUrls: ['./favorites.page.scss'],
    providers: [DestroyService],
    standalone: false
})
export class FavoritesPage implements OnInit {
	constructor(private readonly sharedAdsFacade: SharedAdsFacade, private readonly favoritesFacade: FavoritesFacade) {
		this.adList$ = this.sharedAdsFacade.getAdList$();
		this.isLoading$ = this.sharedAdsFacade.isLoading$();
	}

	currentSection: string;
	adList$: Observable<Ad[]>;
	isLoading$: Observable<boolean> = of(true);

	ngOnInit(): void {
		this.favoritesFacade.loadFavouritesAdList();
	}
}
