import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FavoritesPage } from './favorites.page';
import { RouteDataType } from '@core/services/route.service';

const routes: Routes = [
	{
		path: '',
		component: FavoritesPage,
		data: { [RouteDataType.IS_FAVORITE_BTN_ENABLE]: true, [RouteDataType.SHOW_SELLER_INFO]: true, [RouteDataType.PAGE_REFERENCE]: 'favorites' },
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class FavouritesAdsPageRoutingModule {}
