import { Injectable } from '@angular/core';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { AdsState } from '@shared/states/ads.state';
import { Ad } from '@core/models';
import { finalize, tap } from 'rxjs';

@Injectable()
export class FavoritesFacade {
	constructor(private readonly adsState: AdsState, private readonly adsApi: AdsApi) {}

	loadFavouritesAdList(): void {
		this.adsState.setIsLoading(true);

		this.adsApi
			.getFavourtiesAds()
			.pipe(finalize(() => this.adsState.setIsLoading(false)))
			.subscribe((adList: Ad[]) => this.adsState.setAdList(adList));
	}
}
